#msg {
  text-align: center;
  font-size: 40px;
  color: rgb(15, 216, 149);
}
.intromsg {
  text-align: center;
  font-size: 40px;
  color: rgb(15, 216, 149);
}
.playerinfo {
  display: block;
  align-items: center;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
}
.btn {
  align-content: center;
  color: beige;
  background-color: rgb(9, 10, 10);
  border-radius: 30px;
  padding: 0.5% 2%;
  font-size: large;
}
.btn:hover {
  color: rgb(241, 207, 16);
  cursor: pointer;
}
.button {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 2%;
}
