.box{
    background-color: white;
    border: none;
    border-radius: 10%;
    box-shadow: 0px 0px 8px #888;
    width: 5rem;
    height: 5rem;
    text-align: center;
    font-size: 5em;
    font-weight: bold;
    margin: 0.25em;
    font-family: sans-serif;
}
.box:hover{
    box-shadow: 0px 0px 16px #888;
    cursor: pointer;
}
.x{
    color: black;
}
.o{
    color: black;
}